
import { Movie } from "@/store";
import { Options, Vue } from "vue-class-component";
import { Emit, Prop } from "vue-property-decorator";

@Options({
  components: {},
})
export default class MovieCard extends Vue {
  @Prop()
  record!: Movie;

  @Emit()
  rateMovie(): Movie {
    return this.record;
  }
}
