
import { Options, Vue } from "vue-class-component";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
import { store } from "../store";

@Options({
  components: {
    FontAwesomeIcon,
  },
  props: {},
})
export default class Navbar extends Vue {
  store = store;

  logInClicked(): void {
    store.setLoggedIn("Karol K");
  }

  logOutClicked(): void {
    store.clear();
  }
}
